
import { defineComponent } from 'vue'
import github from '@/services/Github'

export default defineComponent({
  data() {
    return {
      releases: [],
      total: 0
    }
  },
  mounted() {
    this.fetchReleases()
  },
  methods: {
    fetchReleases() {
      github.getRepoReleases('vuetorrent')
        .then((res: any) => {
          this.releases = res.releases
          this.total = res.total
        })
    }
  }
})
